import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { getDataBaki } from "../../../../actions/datamaster_action";
import LaporanDetailBarangExel from "./exel/LaporanTambahBarangExel";
import Tabel from "../../../../components/helpers/tabel";
import CetakLaporanTambahBarang from "./pdf/CetakLaporanTambahBarang";

const maptostate = (state) => {
  return {
    databaki: state.datamaster.getDataBaki,
  };
};
class HeadFilterLaporan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataBaki());
  }
  exportPdf() {
    CetakLaporanTambahBarang(this.props.data);
  }
  render() {
    return (
      <>
        <form
          onSubmit={(data) => this.props.handleSubmit(data)}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
            <div className="col-12 mb-2">
              <label> &nbsp; </label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.submitting}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Memfilter
                  </>
                ) : (
                  "Lihat Laporan"
                )}
              </button>
            </div>
            <div className="col-12">
              <Tabel
                keyField="kode_barcode"
                data={this.props.data}
                columns={this.props.columns}
                empty={true}
                textEmpty="Data Laporan Barang Detail Kosong"
              />
            </div>
            {this.props.export ? (
              <>
                <div className="col-lg-6">
                  <button
                    onClick={() => this.exportPdf()}
                    className="btn btn-warning btn-block"
                  >
                    {" "}
                    Export PDF{" "}
                  </button>
                </div>
                <div className="col-lg-6">
                  <LaporanDetailBarangExel
                    data={this.props.data}
                    kode_baki={this.props.kode_baki}
                  />
                </div>
              </>
            ) : null}
          </div>
        </form>
      </>
    );
  }
}

HeadFilterLaporan = reduxForm({
  form: "HeadFilterLaporan",
  enableReinitialize: true,
})(HeadFilterLaporan);
export default connect(maptostate, null)(HeadFilterLaporan);
